import Vue from 'vue';
import { ButtonGroup, Button, Dropdown, DropdownMenu, DropdownItem, Row, Col, Steps, Step, Tooltip } from 'element-ui';
import { Card, Table, Badge } from 'vue-antd-ui';
import PageHeaderLayout from 'layouts/PageHeaderLayout/index.vue';
import DescriptionList from 'components/DescriptionList';
import Description from 'components/Description';
import AntIcon from 'components/AntIcon';
import Divider from 'components/Divider';
Vue.use(ButtonGroup);
Vue.use(Button);
Vue.use(Dropdown);
Vue.use(DropdownItem);
Vue.use(DropdownMenu);
Vue.use(Row);
Vue.use(Col);
Vue.use(Steps);
Vue.use(Step);
Vue.use(Tooltip);
Vue.use(DescriptionList);
Vue.use(Description);
Vue.use(AntIcon);
Vue.use(Divider);
Vue.component(Card.name, Card);
Vue.component(Table.name, Table);
Vue.component(Badge.name, Badge);
const tabList = [
    {
        key: 'detail',
        tab: '详情'
    },
    {
        key: 'rule',
        tab: '规则'
    }
];
const operationTabList = [
    {
        key: 'tab1',
        tab: '操作日志一'
    },
    {
        key: 'tab2',
        tab: '操作日志二'
    },
    {
        key: 'tab3',
        tab: '操作日志三'
    }
];
const columns = [
    {
        title: '操作类型',
        dataIndex: 'type',
        key: 'type'
    },
    {
        title: '操作人',
        dataIndex: 'name',
        key: 'name'
    },
    {
        title: '执行结果',
        dataIndex: 'status',
        key: 'status',
        scopedSlots: {
            customRender: 'status'
        }
    },
    {
        title: '操作时间',
        dataIndex: 'updatedAt',
        key: 'updatedAt'
    },
    {
        title: '备注',
        dataIndex: 'memo',
        key: 'memo'
    }
];
export default Vue.extend({
    components: {
        PageHeaderLayout
    },
    data() {
        return {
            tabList: tabList,
            headerTabKey: 'detail',
            operationTabList: operationTabList,
            operationKey: 'tab1',
            columns: columns
        };
    },
    computed: {
        advancedOperation1() {
            return this.$store.state.profile.advancedOperation1;
        },
        advancedOperation2() {
            return this.$store.state.profile.advancedOperation2;
        },
        advancedOperation3() {
            return this.$store.state.profile.advancedOperation3;
        },
        activeOperationTableData() {
            const { operationKey, advancedOperation1, advancedOperation2, advancedOperation3 } = this;
            switch (operationKey) {
                case 'tab1':
                    return advancedOperation1;
                case 'tab2':
                    return advancedOperation2;
                case 'tab3':
                    return advancedOperation3;
                default:
                    return [];
            }
        }
    },
    mounted() {
        this.$store.dispatch('profile/fetchAdvanced');
    },
    methods: {
        onTabChange(key) {
            this.headerTabKey = key;
        },
        onOperationTabChange(key) {
            this.operationKey = key;
        }
    }
});



